<template>
    <v-app>
      <v-card class="mx-auto" max-width="400">
        <v-carousel
          hide-delimiters
          :show-arrows="false"
          height="700px"
          cycle
          :interval="3000"
        >
          <v-carousel-item
            v-for="(item, i) in items"
            :key="i"
            :src="item.src"
            height="200px"
            reverse-transition="fade-transition"
            transition="fade-transition"
          >
            <v-card-title class="white--text" style="position: absolute; top: 80px; font-size: 40px; font-family: 'Arial Black', sans-serif; text-shadow: 2px 2px 4px #000;">
              {{ item.text }}
            </v-card-title>
          </v-carousel-item>
        </v-carousel>
        <br>
        <br>
        <v-card-actions class="d-flex justify-end">
          <div>
            <v-btn @click="openDialog" x-large icon text color="orange">
              Next
              <v-icon>mdi-chevron-right</v-icon>
            </v-btn>
          </div>
        </v-card-actions>
        <br>
      </v-card>
  
      <!-- 彈跳視窗 -->
      <v-dialog v-model="dialog" max-width="400">
        <v-card>
          <v-card-title class="headline">確認操作</v-card-title>
          
          <v-card-text>
            <v-checkbox
              v-model="agreed"
              :input-value="agreed ? true : Noagreed ? false : null"
              label="我同意幸福下去"
              @change="handleAgreementChange('agree')"
            ></v-checkbox>
            <v-checkbox
              v-model="Noagreed"
              :input-value="Noagreed ? true : agreed ? false : null"
              label="不同意幸福"
              @change="handleAgreementChange('disagree')"
            ></v-checkbox>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" text @click="confirmAgreement">確認</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
  
      <!-- 不同意提示的彈跳視窗 -->
      <v-dialog v-model="disagreeDialog" max-width="400">
        <v-card>
          <v-card-title class="headline">提示</v-card-title>
          <v-card-text>
           妳只能同意 夠霸道嗎
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="red darken-1" text @click="closeDisagreeDialog">確認</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-app>
  </template>
  
  <script>
  export default {
    data() {
      return {
        items: [
          {
            src: 'https://lh3.googleusercontent.com/pw/AP1GczMxG94Ty-ZZqRUtDQokuQbFAArAXV-aBLXxjaW9Ew_cFuhTPbzeRLWj1v1G6YXK7_mdAVp3ItaJg69YZ-4I6gy2s57QJtEcOR34IZhcbRo6IwnEmH897a4EbXlS0-zfS0MR8kktzObi4FW_Hg5pY0JB=w1950-h1462-s-no-gm?authuser=0',
            text: '想和妳一直走下去'
          },
          {
            src: 'https://lh3.googleusercontent.com/pw/AP1GczPI0tyXdaY1IcrDhgw9Rr1e6INBGBf1QRDE-HOZ_Jwws7ZwedhRNoOYbwwngBZ9gKRGgc01mt5CsBDJI-V6L89ziHpdH5abwrw1p6QzvpshYbQYoBd9dClckI31dfOrzUesjRkvuhzbs3fLGeVjIZB8=w1479-h1109-s-no-gm?authuser=0',
            text: '去所有想去的地方'
          },
          {
            src: 'https://lh3.googleusercontent.com/pw/AP1GczMbDeCiTYAhypLeunBXeR18egZiuYChUriZqT_MFqqwcxU7XWfwIoeZUzgn_YasRi7lqsLT-3YQXFHURDd_1jBRo2kaZZT8oUqN4rI8BvUdaX-CiiBc1B5rkxZV5NL4rIXc6miuXES_zHBBcZVdsU4E=w1096-h1462-s-no-gm?authuser=0',
            text: '養一堆可愛大狗狗'
          },
          {
            src: 'https://lh3.googleusercontent.com/pw/AP1GczMa8vpfkgH7xv9y-R8hVWo8qjcFgDeGTPPcGNQLIAP1j9d-ExP7YGeem8SaFIBu-TIdaJdDQaz74njNxEfDB4tkmGfJBZ5RrL4RNFryJpTob_m73a4_G1FFroB8nl3q60DFeeIipATj0sceTFOokGAY=w1096-h1462-s-no-gm?authuser=0',
            text: '吃遍所有的美食'
          },
          {
            src: 'https://lh3.googleusercontent.com/pw/AP1GczOGeYdIs9U1yutiCp_K18aRXAU7O4gt1EHZUThdQSR0-sAzDDPZ0z6OMcmak-yspfCJ09XfV-t8yeMnuKQex8zQcaQTwy47rwsHQU2eU8H2j6ecKEJvWFyLfEmzqgM_6LYhlHNyXDetGwYs-yQpeR0a=w1102-h1462-s-no-gm?authuser=0',
            text: '快快樂樂的過每一天'
          },
        ],
        dialog: false, // 控制彈跳視窗顯示
        disagreeDialog: false, // 控制不同意提示的彈跳視窗顯示
        agreed: false, // 用於 "我同意" 复选框的状态
        Noagreed: false, // 用於 "不同意" 复选框的状态
      };
    },
    methods: {
      openDialog() {
        this.dialog = true;
      },
      handleAgreementChange(type) {
        if (type === 'agree') {
          this.Noagreed = false; // 取消选择不同意
        } else if (type === 'disagree') {
          this.agreed = false; // 取消选择同意
          this.dialog = false; // 关闭当前对话框
          this.disagreeDialog = true; // 显示不同意提示的对话框
        }
      },
      confirmAgreement() {
        if (this.agreed) {
          this.dialog = false;
          this.$router.push('/about');
        } else {
          alert('請先勾選 "我同意"');
        }
      },
      closeDisagreeDialog() {
        this.disagreeDialog = false;
        this.dialog = true; // 重新打开选择对话框
      }
    }
  }
  </script>
  