import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from "@/views/home";
import About from "@/views/about";
import User from "@/views/user";

 Vue.use(VueRouter);

const routes = [
   {
        path:"/",
        name:'Home',
        component:Home
    },
    {
    path:"/about",
    name:'About',
    component:About
    },
    {
    path:"/user",
    name:'User',
    component:User
}
]
//创建路由
const router = new VueRouter({
    mode:'hash', // 改為 'hash' 模式
    routes
})
//导出
export default router;