        <v-dialog v-model="dialog" max-width="290">
          <v-card >
            <v-card-title class="headline">恭喜曾小姐！</v-card-title>
            <v-card-text>
                <div style="display: flex; align-items: center; justify-content: space-between;">
                    <div>
                    獲得了<strong>{{ prizeName }}</strong>
                    </div>
                    <img 
                    width="100px" 
                    height="150px" 
                    src="https://lh3.googleusercontent.com/pw/AP1GczOpVS7gekLhywMXd_D4Sxh-DRg4HeK1dmlmxdKOboxEWozoI_ygkpYE_A-izCh1gUZcMt3PtXjDPzIzRrx1zaljvqrMkSc9YvNYx2sYWzWhNQsztZCxnx5dsqS8AZ-iWVo2JyPbc2BWS3WMWBvLzZVt=w822-h1460-s-no-gm?authuser=0" 
                    alt="prize image"
                    style="margin-left: 15px; border-radius: 8px;"
                    >
                </div>
                </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="black" text @click="backhome()">確認</v-btn>
            </v-card-actions>
          </v-card>
