      </div>
    </v-app>
  </template>
  
  <script>
  export default {
    data() {
      return {
        items: [
          { index: 1, img: 'https://lh3.googleusercontent.com/pw/AP1GczMKfCk2Hc8DMmuooeM_DdcqQkf7ZHzxiYab1it130TIKMZH6_Hijh6ToIT6_2JL2HbD1cTRU3vnJwykhErutlbkdl9C72eX7WsCEoNoM9CL7EDNptJUrL3nO0XUJ9kz4HSt7hSCfjanWFk2qMC8HUKs=w822-h1460-s-no-gm?authuser=0', name: '' },
          { index: 2, img: 'https://lh3.googleusercontent.com/pw/AP1GczNOTGMagoea_OJ7cx8HOUdhlN1gCryt8vxPb0dqiF6TeXuzsXqkhvZWD49m6f748lHGlisUDLP2J0eOxOTn81YV_Apz7Fw4fY_GrQbqB4ZAGYywIc4l0F0khZfAp7s81_by0TjL_80dMjYBKhfEacHq=w822-h1460-s-no-gm?authuser=0', name: '' },
          { index: 3, img: 'https://lh3.googleusercontent.com/pw/AP1GczMq37yzyubhScoCkfyw-cp-SGNRFCON0eMr1UI-5KuloC4OCPdaDTpZIlL6Cvqq9hPbd20PlAfGrQzN6ON0x07DR1QqxmBDwFxxqppGy0TjphqLfExMVXr_vqlg65ZeMpiP7V9tpLXsL9LuxTwI82Z_=w822-h1460-s-no-gm?authuser=0', name: '' },
          { index: 4, img: 'https://lh3.googleusercontent.com/pw/AP1GczNozy0Y_tG1AywmLkZVWqSD4qDljqn_Zr0u5l9FQE5xoT01-S8OlUpr99drycuYGqWJpa91DgxOPaqQzy8C-dzgSSCZZq_HuHJNHbz7ghQvaOklj72kzrb4iTYIzD1cBZDEb4973PcWh4Z3jf0jjpb7=w822-h1460-s-no-gm?authuser=0', name: '' },
          { index: 5, img: 'https://lh3.googleusercontent.com/pw/AP1GczOpVS7gekLhywMXd_D4Sxh-DRg4HeK1dmlmxdKOboxEWozoI_ygkpYE_A-izCh1gUZcMt3PtXjDPzIzRrx1zaljvqrMkSc9YvNYx2sYWzWhNQsztZCxnx5dsqS8AZ-iWVo2JyPbc2BWS3WMWBvLzZVt=w822-h1460-s-no-gm?authuser=0' , name: '日本來回機票'},
          { index: 6, img: 'https://lh3.googleusercontent.com/pw/AP1GczNJno_wgGl11XoPX4KdGqd4bxshD_vj5zOzU3TVgLplq10B7yJMYQgSfhe18VJrHAcnRG2nLuxaQYx2_zeQJE-AKLNPhiWrZnqVPuuWEaHt7IM-HhubeGF-VUZPF6pz6PLnyZMz-ww7A9WgW9qH03AF=w822-h1460-s-no-gm?authuser=0' , name: ''},
          { index: 7, img: 'https://lh3.googleusercontent.com/pw/AP1GczN1cpgGR97GXpXUgZk45gR8xkruNT_vzEWx3Dl-3eHly8c47gambGmQTGpXbc4WNvcAyl1YqFy5gveWZxA1fcKVrgIAg7UlzalAXeT8qqUdx08zbVXLNqqe57Rlk7ZD1mYRjzEZ4l3GXFVHTFvZFQJQ=w822-h1460-s-no-gm?authuser=0' , name: ''},
          { index: 8, img: 'https://lh3.googleusercontent.com/pw/AP1GczM1gx5S1CaT1pjbeBXMQWbFAIkqj6MfGSHrM5uE_w7iJ6vIeknFrtc06-0RZYYOXARDTqanJnH3kWO-rmF6ehrgQFJz7A7J9qkpCWCkCkcVpQyjhnVUEQojzAbE2X1e-dvTl40eWJAaRD3Y4bSu7Z-E=w822-h1460-s-no-gm?authuser=0' , name: ''},
          { index: 9, img: 'https://lh3.googleusercontent.com/pw/AP1GczNr3aeh_eNvZv_s_w8ldMtRTgcSRlVExQTl9ZQyjt7I9CfvJYrym1bq0uKuwOdH1QeLvIuYFyR1svTk5DBbsdro141ffneFqm9xkPNWJCAOzB0XKnlpelWV3duRQc5rmVumfNeG0FT6rXksv6a9ctYa=w822-h1460-s-no-gm?authuser=0' , name: ''},
        ],
        activeIndex: 1,
        prize: '',
        prizeName: '',
        dialog: false, 
        LOOP_NUM: 5,
        UNIT_NUM: 8,
        DURATION: 6000,
        start: 0,
        end: 0,
      };
    },
    methods: {
      backhome(){
        this.dialog = false
        this.$router.push('/');
      },
      move(pos) {
        pos = pos % this.UNIT_NUM;
        this.activeIndex = pos + 1;
      },
      run() {
        let startTime = new Date();
        this.start = this.start % this.UNIT_NUM;
        
        this.end = 4 + this.LOOP_NUM * this.UNIT_NUM;  // 设置end，使其最终落在第5个奖品
        this.prize = 5; 
        let handle = setInterval(() => {
          let curTime = new Date();
          let duration = curTime.getTime() - startTime.getTime();
          let percent = duration / this.DURATION;
          percent = 1 - Math.pow(1 - percent, 1.4);
  
          if (duration >= this.DURATION) {
            clearInterval(handle);
            percent = 1;
            this.prizeName = this.items[this.prize - 1].name;
            this.dialog = true; // 显示中奖结果弹跳视窗
          }
          this.start = Math.floor(this.end * percent);
          this.move(this.start);
        }, 1000 / 60);
      },
    },
  };
  </script>
  
  <style scoped>
  .lucky-draw {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f5f5;
  }
  
  .lucky-draw-container {
    width: 400px;
    height: 800px;
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
    display: flex;
    flex-direction: column;
    padding: 20px;
    box-sizing: border-box;
  }
  
  .title {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
  }
  
  .prize-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 10px;
    flex-grow: 1;
  }
  
  .prize-item {
    background-color: #f8f8f8;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
    overflow: hidden; /* 确保图片溢出部分被裁剪 */
  }
  
  .prize-item.active {
    transform: scale(0.95);
    box-shadow: 0 0 0 3px #ff6b6b;
  }
  
  .prize-content {
    width: 100%; /* 确保图片和内容充满整个格子 */
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative; /* 确保文字内容在图片之上 */
  }
  
  .prize-item img {
    width: 100%;
    height: 100%;
    object-fit: cover; /* 确保图片充满格子且保持比例 */
    position: absolute; /* 确保图片在内容下方 */
    top: 0;
    left: 0;
  }
  
  .prize-item p {
    font-size: 12px;
    color: #fff;
    margin: 0;
    z-index: 1; /* 确保文字显示在图片上方 */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* 添加文字阴影以便在图片上更清晰 */
  }
  
  .control-area {
    margin-top: 20px;
    text-align: center;
  }
  
  .v-btn {
    margin-bottom: 15px;
  }
  
  .result-display {
    font-size: 18px;
    color: #2d3436;
  }
  
  .result-display span {
    font-weight: bold;
    color: #ff6b6b;
  }
  </style>
  